.signage {
    font-weight: 600;
    justify-content: center;
    display: grid;
    padding: 8px;
    background: #00c4cc;
    color: #ffffff;
    border-radius: 8px;
    margin-bottom: 20px;
}
.sales-header, .list-table{
    padding: 20px 20px 20px 20px;
    position: relative;
    z-index: 1;
    background: #ffffff;
    border-radius: 5px;
}

.list-table h5 {
    margin-bottom: 15px;
    color: #545b62;
    font-weight: 300;
}

.card.card-wrapper {
    border: 1px solid #e9ecef59;
    box-shadow: 1px 1px 2px 2px #e9ecef78;
}

.card .form-control {
    font-size: 0.7em;
}

@media screen and (max-width: 768px) {
    .sales-header h3 {
        font-size: 1.55rem;
    }
    .list-table h5 {
        font-size: 1.05rem;
    }
}